.banner {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../images/banner.jpg");
  max-height: fit-content;
  height: 700px;
  background-position: center center, center top;
  background-repeat: no-repeat;
  width: 100%;
  opacity: 1.7;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h1 {
    text-align: center;
    left: 25%;
    color: white;
    font-family: "Baskerville Old Face Bold";
    font-size: 60px;
    font-weight: lighter;
  }
  h3 {
    text-align: center;
    margin-top: 10%;
    left: 25%;
    color: white;
    font-family: "Josefin Sans Light";
    font-weight: lighter;
    font-size: 30px;
  }
  button {
    transform: translate(5%);
    color: #323b26;
    //color: #ffffff;
    //color: #100a0a;
    //color: #0079a8;
    background-color: #e8e909;
    border: 1px solid #e8e909;
    border-radius: 8px;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    outline: none;
    //position: absolute;
    padding: 13px 23px;
    text-align: center;
    text-decoration: none;
    touch-action: manipulation;
    transition: box-shadow 0.2s, -ms-transform 0.1s, -webkit-transform 0.1s,
      transform 0.1s;
    user-select: none;
    -webkit-user-select: none;
    width: auto;
    opacity: 1;
    z-index: 1000;
    margin-bottom: 10%;
  }
  button:hover {
    background-color: #f6f614;
  }
}

.buttons button {
  margin: 5px;
}
